import React from "react";
import {Link} from 'react-router-dom';

const Footer = () => {
    return ( 
        <div className="navbar navbar-expand-lg bg-black bottom-container d-flex bg-dark justify-content-evenly">
            <Link className="navbar-brand" to="/">
            <h1 className="text-danger">
                <i className="bi bi-display"></i>
            </h1>
            </Link>
        <div>
            <h4 className="text-light">Copyright 2022</h4>
        </div>
        <div className="h1">
            <a href="https://facebook.com/manjyomethunder" target="_blank" rel="noreferrer" title="Facebook"> <i className="bi bi-facebook text-danger footerLogo"></i></a>
            <a href="https://instagram.com/manjyomethunder" target="_blank" rel="noreferrer" title="Instagram"> <i className="bi bi-instagram text-danger footerLogo"></i></a>
            <a href="https://twitter.com/manjyomethunder" target="_blank" rel="noreferrer" title="Twitter"> <i className="bi bi-twitter text-danger footerLogo"></i></a>
        </div>
    </div>
     );
}
 
export default Footer;