import React from "react";
import { useNavigate } from 'react-router-dom';

function Backbutton() {
    const navigate = useNavigate();
    return ( 
        <div class="container fixed-bottom">
        
        <h1 class="bi bi-arrow-left-square btn bg-danger text-light" onClick={() => navigate(-1)}>  Back
        </h1>
        
    </div>
    
     );
     //document.querySelector('bi-arrow-left-square').onClick = "history.back()";
}
 
export default Backbutton;