import React from "react";
import {Link} from 'react-router-dom';
import {HashLink} from 'react-router-hash-link';


const HeadingPart = () => {
    return (
        <nav className="navbar navbar-expand-lg bg-black fixed-top">
        <div className="container-fluid">
            <HashLink className="navbar-brand" to="/#top">
                <h1 className="text-menu" id="navbar-brand">
                    <span id="letter-a">A</span><span className="letter-n text-light">N</span><span className="text-light">IME</span>FL<span className="letter-i">I</span><span id="letter-x">X</span> <i className="bi bi-display"></i>
                </h1>
            </HashLink>
            <button class="navbar-toggler" data-bs-toggle="collapse" data-bs-target="#navbarExample">
                <span class="navbar-toggler-icon bg-menu"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarExample">
                <ul className="navbar-nav mx-auto text-menu h1">
                    <li className="nav-item text-menu">
                        <HashLink className="nav-link text-menu" to="/#top">HOME</HashLink>
                    </li>
                    <li className="nav-item text-menu">
                        <HashLink className="nav-link text-menu" to="/#top5" smooth>Top 5 Anime</HashLink>
                    </li>
                    <li className="nav-item dropdown">
                        <Link className="nav-link dropdown-toggle text-menu show" to="#" id="dropDownSample" data-bs-toggle="dropdown">
                            List
                        </Link>
                        <ul className="dropdown-menu bg-dark" aria-labelledby="dropDownSample">
                            <li className="list-item">
                                <Link className="dropdown-item text-menu h1" to="Naruto">Naruto</Link>
                            </li>
                            <li className="list-item">
                                <Link className="dropdown-item text-menu h1" to="Hunterx">Hunter X Hunter</Link>
                            </li>
                            <li className="list-item">
                                <Link className="dropdown-item text-menu h1" to="Deathnote">Death Note</Link>
                            </li>
                            <li className="list-item">
                                <Link className="dropdown-item text-menu h1" to="Haikyu">Haikyu</Link>
                            </li>
                            <li className="list-item">
                                <Link className="dropdown-item text-menu h1" to="Codegeass">Code Geass</Link>
                            </li>
                        </ul>
                    </li>
                </ul>
                <form className="input-group search-top" style={{maxWidth: "200px"}}>
                <input type="text" className="form-control bg-dark text-light" placeholder="Search..." />
                
                <button className="btn" id="btn-danger"><i className="bi bi-search"></i></button>
            </form>
            </div>
            
            
        </div>
        
    </nav>
        
    );
}

export default HeadingPart;