import React from "react";

import {Link} from 'react-router-dom';
import hxh03 from "../images/hxh03.jpg";
import naruto03 from "../images/naruto03.jpg";
import deathnote03 from "../images/deathnote03.jpg";
import codegeass03 from "../images/codegeass03.jpg";
import haikyu03 from "../images/haikyu03.jpg";



const Grid = () => {
    return ( 
        <div id="top5" style={{boxShadow: "black solid 1px"}}><br /><br />
        <h1 className="container text-light text-h1">TOP 5 ANIME</h1>
    <br/><br/>
    <div className="row">
        <div className="col-md-2">
        </div>
        <div className="col-md-8">
           <div className="row">
                <div className="col-md-4 mb-5">
                    <div className="card px-lg-5 bg-black border-none">    
                        <img src={naruto03} className="card-img-top" alt=""/>
                        <div className="card-body bg-dark">
                            <h3 className="card-title text-light">Naruto</h3>
                            <p className="card-text text-light">
                                Naruto: Shippuden is an anime series mainly adapted from Part II of Masashi Kishimoto's original manga series, with exactly 500 episodes. It is set two and a half years after the original series in the Naruto universe, following the ninja teenager Naruto Uzumaki and his allies.
                            </p>
                            <Link to="/Naruto" className="btn text-light stretched-link" id="btn-danger">More Details <i className="bi bi-info-square"></i></Link>
                        </div> 
                    </div>
                </div>
                <div className="col-md-4 mb-5">
                    <div className="card px-lg-5 bg-black border-none">    
                        <img src={hxh03} className="card-img-top" alt=""/>
                        <div className="card-body bg-dark">
                            <h3 className="card-title text-light">Hunter X Hunter</h3>
                            <p className="card-text text-light">
                                Hunter  Hunter (pronounced "hunter hunter") is a Japanese manga series written and illustrated by Yoshihiro Togashi. It has been serialized in Shueisha's shnen manga magazine Weekly Shnen Jump since March 1998, although the manga has frequently gone on extended hiatuses since 2006.
                            </p>
                            <Link to="Hunterx" className="btn text-light stretched-link" id="btn-danger">More Details <i className="bi bi-info-square"></i></Link>
                        </div>   
                    </div>
                </div>
                <div className="col-md-4 mb-5">
                    <div className="card px-lg-5 bg-black border-none">    
                        <img src={deathnote03} className="card-img-top" alt=""/>
                        <div className="card-body bg-dark">
                            <h3 className="card-title text-light">Death Note</h3>
                            <p className="card-text text-light">
                                Death Note (stylized in all caps) is a Japanese manga series written by Tsugumi Ohba and illustrated by Takeshi Obata. The story follows Light Yagami, a teen genius who discovers a mysterious notebook: the "Death Note", which belonged to the Shinigami Ryuk, and grants the user the supernatural ability to kill anyone whose name is written in its pages.
                            </p>
                            <Link to="Deathnote" className="btn text-light stretched-link" id="btn-danger">More Details <i className="bi bi-info-square"></i></Link>
                        </div>  
                    </div>
                </div>
            </div>
        </div>
        <div className="col-md-2 col2pad">
        </div>
    </div>
    <div className="row">
        <div className="col-md-2">
        </div>
        <div className="col-md-8">
           <div className="row">
            <div className="col-md-2"></div>
                <div className="col-md-4 mb-5">
                    <div className="card px-lg-5 bg-black border-none">    
                        <img src={haikyu03} className="card-img-top" alt=""/>
                        <div className="card-body bg-dark">
                            <h3 className="card-title text-light">Haikyu</h3>
                            <p className="card-text text-light">
                                Haikyu!! (!!, Haiky!!, from the kanji  "volleyball") is a Japanese manga series written and illustrated by Haruichi Furudate. The story follows Shoyo Hinata, a boy determined to become a great volleyball player despite his small stature. It was serialized in Shueisha's Weekly Shnen Jump from February 2012 to July 2020, with its chapters collected in forty-five tankbon volumes.
                            </p>
                            <Link to="Haikyu" className="btn text-light stretched-link" id="btn-danger">More Details <i className="bi bi-info-square"></i></Link>
                        </div> 
                    </div>
                </div>
                <div className="col-md-4 mb-5">
                    <div className="card px-lg-5 bg-black border-none">    
                        <img src={codegeass03} className="card-img-top" alt=""/>
                        <div className="card-body bg-dark">
                            <h3 className="card-title text-light">Code Geass</h3>
                            <p className="card-text text-light">
                                Code Geass: Lelouch of the Rebellion (Japanese:  , Hepburn: Kdo Giasu: Hangyaku no Rurshu), often referred to simply as Code Geass, is a Japanese anime television series produced by Sunrise. It was directed by Gor Taniguchi and written by Ichir kouchi, with original character designs by Clamp. Set in an alternate timeline, it follows the exiled prince Lelouch vi Britannia, who obtains the "power of absolute obedience" from a mysterious woman named C.C.
                            </p>
                            <Link to="Codegeass" className="btn text-light stretched-link" id="btn-danger">More Details <i className="bi bi-info-square"></i></Link>
                        </div>
                    </div>
                </div>
                <div className="col-md-2"></div>
            </div>
        </div>
        <div className="col-md-2 col2pad">
        </div>
    </div>
    
    </div>
     );
}
 
export default Grid;