import React from "react";

const Statictext = () => {
    return ( 
        <div className="main-text">
            <div className="col-md-12 text-center">
                <h1 className="text-static shadow-lg">
                    Unlimited movies, Anime <br/> TV series, and more.
                </h1>
                <br/><br/>
                <h3>
                    Ready to watch? Enter your Email Address
                </h3>
                <form className="container input-group">
                    <div className="col-lg-4"></div>
                    <div className="col-lg-4">
                        <input type="email" className="form-control bg-light form-control-lg" placeholder="Email" />
                        <button className="btn bg-danger text-light" id="btn-danger">Get Started</button>
                    </div>
                    <div className="col-lg-4"></div>
                </form>
            </div>
        </div>
     );
}
 
export default Statictext;