import React from "react";
//import Banner from "../images/naruto01.png"
import naruto01 from "../images/naruto01.png";
import hxh01 from "../images/hxh01.png";
import deathnote01 from "../images/deathnote01.png";
import haikyu01 from "../images/haikyu01.png";
import codegeass01 from "../images/codegeass01.png";


const ImageLayout = () => {
    return (
        // <div className="imageContainer" id="top">
        //     <img src={Banner} alt="" className="imageContainer d-block w-100" />

        // </div>
        <div id="carouselSlide" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-indicators">
            <button data-bs-target="#carouselSlide" data-bs-slide-to="0" className="active"></button>
            <button data-bs-target="#carouselSlide" data-bs-slide-to="1"></button>
            <button data-bs-target="#carouselSlide" data-bs-slide-to="2"></button>
            <button data-bs-target="#carouselSlide" data-bs-slide-to="3"></button>
            <button data-bs-target="#carouselSlide" data-bs-slide-to="4"></button>
        </div>
        <div className="carousel-inner">
            <div className="carousel-item active">
                <img src={naruto01} alt="" className="d-block w-100" />
            </div>
            <div className="carousel-item">
                <img src={hxh01} alt="" className="d-block w-100" />
            </div>
            <div className="carousel-item">
                <img src={deathnote01} alt="" className="d-block w-100" />
            </div>
            <div className="carousel-item">
                <img src={haikyu01} alt="" className="d-block w-100" />
            </div>
            <div className="carousel-item">
                <img src={codegeass01} alt="" className="d-block w-100" />
            </div>
        </div>
        <button className="carousel-control-prev" type="button" data-bs-target="#carouselSlide" data-bs-slide="prev">
            <span className="carousel-control-prev-icon"></span>
            <span className="visually-hidden">Previous</span>
        </button>
        <button className="carousel-control-next" type="button" data-bs-target="#carouselSlide" data-bs-slide="next">
            <span className="carousel-control-next-icon"></span>
            <span className="visually-hidden">Next</span>
        </button>
    </div>
    );
}

export default ImageLayout;